<template>
  <v-container>
    <!--横幅-->
    <v-row>
      <v-col>
        <v-banner two-line>
          <!--小图标-->
          <v-avatar slot="icon" color="black" size="40">
            <v-icon color="white">mdi-account-box</v-icon>
          </v-avatar>
          <!--文字介绍-->
          <h2>TEAMS MANANGEMENT</h2>
          <template v-slot:actions>
            <v-btn text class="pa-2" @click="joinTeam">join team</v-btn>
            <v-btn text class="pa-2" @click="createTeam">create team</v-btn>
          </template>
        </v-banner>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <!-- 侧边栏 -->
        <v-navigation-drawer
          color="rgba(255,255,255,0.0)"
          height="100%"
          permanent
        >
          <v-list flat>
            <v-list-item>
              <h3>MY TEAMS:</h3>

              <v-spacer></v-spacer>
              <v-btn text @click="initialize"
                ><v-icon>mdi-autorenew</v-icon>
              </v-btn>
            </v-list-item>
            <!-- 分割线 -->
            <v-divider></v-divider>
            <!-- 队伍列表 -->
            <v-list-item-group color="indigo" v-model="teamNumber" mandatory>
              <v-list-item v-for="(item, i) in teamList" :key="i">
                <v-list-item-icon>
                  <v-icon>mdi-account-group</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                  <v-list-item-title v-text="item.teamName"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
            <!-- <v-simple-table
              height="430px"
              style="background-color: rgba(255, 255, 255, 0.1)"
            >
              <template v-slot:default>
                <thead>
                  <tr>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in teamList" :key="item.teamName">
                    <td>
                      <h3>
                        <v-icon>mdi-account-group</v-icon>{{ item.teamName }}
                      </h3>
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table> -->
          </v-list>
        </v-navigation-drawer>
      </v-col>
      <v-col>
        <!-- 团队信息卡片 -->
        <v-card
          class="elevation-8"
          width="800"
          height="500"
          color="rgba(255,255,255,0.3)"
          shaped
        >
          <!-- 团队信息卡片 -->
          <v-card-title
            >TEAM INFORMATION:
            <v-spacer></v-spacer>
            <v-btn
              text
              color="red lighten-1"
              v-if="useringName == teamList[teamNumber].creater"
              @click="dismissTeam(teamList[teamNumber])"
              >Dismiss Team</v-btn
            >
            <v-btn
              text
              color="red lighten-1"
              v-else
              @click="leaveTeam(teamList[teamNumber])"
              >Leave Team</v-btn
            >
          </v-card-title>
          <!-- 卡片内容 -->
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="6">
                  <!-- 团队文件 -->
                  <v-row
                    ><v-col
                      ><h2 style="color:black">
                        Files:<v-btn
                          text
                          @click="
                            open('tFileManage', {
                              teamName: teamList[teamNumber].teamName,
                            })
                          "
                          ><v-icon>mdi-file-outline</v-icon
                          ><v-icon>mdi-arrow-right</v-icon></v-btn
                        >
                      </h2>
                      <br />
                      <v-divider></v-divider></v-col
                  ></v-row>
                  <!-- 团队名 -->
                  <v-row
                    ><v-col
                      ><h2 style="color:black">TeamName:{{ teamList[teamNumber].teamName }}</h2>
                      <br />
                      <br />
                      <v-divider></v-divider></v-col
                  ></v-row>
                  <!-- 创建时间 -->
                  <v-row
                    ><v-col
                      ><h2 style="color:black">CreateTime:{{ teamList[teamNumber].createTime }}</h2>
                      <br /><br />
                      <v-divider></v-divider></v-col
                  ></v-row>
                  <!-- 创建者 -->
                  <v-row
                    ><v-col
                      ><h2 style="color:black">Creater:{{ teamList[teamNumber].creater }}</h2>
                      <br /><br />
                      <v-divider></v-divider></v-col
                  ></v-row>
                  <!-- 团队序号 -->
                  <v-row
                    ><v-col
                      ><h2 style="color:black">TeamID:{{ teamList[teamNumber].team_id }}</h2>
                      <br /><br />
                      <v-divider></v-divider></v-col
                  ></v-row>
                </v-col>
                <v-col cols="6">
                  <!-- 团队用户列表 -->
                  <v-card color="rgba(255,255,255,0.1)">
                    <v-card-title>
                      <h3 style="color: black">Members:</h3>
                      <!-- 搜索栏 -->
                      <v-text-field
                        v-model="search"
                        append-icon="mdi-magnify"
                        label="Search"
                        single-line
                        hide-details
                      ></v-text-field>
                    </v-card-title>
                    <v-data-table
                      :headers="userHeader"
                      :items="teamList[teamNumber].members"
                      :search="search"
                      hide-default-header
                      hide-default-footer
                      style="background-color: rgba(255, 255, 255, 0.1)"
                      height="300"
                    >
                    </v-data-table>
                  </v-card>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <!-- 通用对话框 -->
    <v-dialog
      v-model="aDialog"
      max-width="500px"
      transition="dialog-bottom-transition"
      persistent
    >
      <v-card>
        <!-- 对话框标题 -->
        <v-card-title class="headline" v-if="state == 'join'"
          >Please enter a TeamID:</v-card-title
        >
        <v-card-title class="headline" v-else-if="state == 'leave'"
          >Do you really want to leave:{{
            teamList[teamNumber].teamName
          }}</v-card-title
        >
        <v-card-title class="headline" v-else-if="state == 'dismiss'"
          >Do you really want to Dismiss your team?</v-card-title
        >
        <v-card-title class="headline" v-else-if="state == 'create'"
          >Creating,please give your team a TeamName?</v-card-title
        >
        <!-- 加入队伍输入表单 -->
        <v-card-text>
          <v-form v-model="valid"
            ><v-text-field
              v-if="state == 'join'"
              :rules="rules.teamIDRules"
              v-model="editedItem.team_id"
            ></v-text-field
          ></v-form>
          <!-- 解散队伍确认表单 -->
          <v-form v-model="valid"
            ><v-text-field
              v-if="state == 'dismiss'"
              :rules="rules.teamIDRules"
              v-model="DismissConfirming"
            ></v-text-field
          ></v-form>
          <!-- 新建队伍命名表单 -->
          <v-form v-model="valid"
            ><v-text-field
              v-if="state == 'create'"
              :rules="rules.teamNameRules"
              v-model="editedItem.teamName"
            ></v-text-field
          ></v-form>
        </v-card-text>

        <!-- 按钮 -->
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeDialog">Cancel</v-btn>
          <v-btn
            v-if="state == 'leave'"
            color="red darken-1"
            text
            :disabled="!valid"
            @click="leaveTeamConfirm"
            >Leave</v-btn
          >
          <v-btn
            v-if="state == 'create'"
            color="green darken-1"
            text
            :disabled="!valid"
            @click="createTeamConfirm"
            >Create</v-btn
          >
          <v-btn
            v-if="state == 'dismiss'"
            color="red darken-1"
            text
            :disabled="!valid"
            @click="dismissTeamConfirm"
            >Dismiss</v-btn
          >
          <v-btn
            v-else-if="state == 'join'"
            color="green darken-1"
            text
            :disabled="!valid"
            @click="joinTeamConfirm"
            >Join</v-btn
          >
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- 通用提示框 -->
    <v-snackbar v-model="snackbar">
      {{ snackbarData }}
      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>
<script>
export default {
  name: "teamManange",
  data: () => ({
    //当前选中的队伍
    teamNumber: 0,
    //搜索栏
    search: "",
    //队伍列表
    teamList: [
      {
        teamName: "DEFAULT TEAM",
        createTime: "",
        creater: "Visitor",
        team_id: "join a team~",
        members: [
          { userName: "NAME1", userEmail: "Email@qq.com" },
          { userName: "NAME2", userEmail: "Email@qq.com" },
          { userName: "NAME3", userEmail: "Email@qq.com" },
        ],
      },
    ],
    defaultList: [
      {
        teamName: "DEFAULT TEAM",
        createTime: "",
        creater: "Visitor",
        team_id: "join a team~",
        members: [
          { userName: "NAME1", userEmail: "Email@qq.com" },
          { userName: "NAME2", userEmail: "Email@qq.com" },
          { userName: "NAME3", userEmail: "Email@qq.com" },
        ],
      },
    ],
    //消息栏
    snackbar: false,
    snackbarData: "",
    //加入，退出对话框
    aDialog: false,
    state: "", //有join、leave,create种状态
    //用户表表头
    valid: true,
    //解散队伍确认
    DismissConfirming: "If you do,enter your TeamID",
    userHeader: [
      {
        text: "userName",
        align: "start",
        value: "userName",
      },
      {
        text: "email",
        value: "userEmail",
      },
    ],
    //处理件
    editedItem: {
      teamName: "",
      createTime: "",
      creater: "",
      team_id: "",
      members: [],
    },
    defaultItem: {
      teamName: "",
      createTime: "",
      creater: "",
      team_id: "",
      members: [],
    },
    rules: {
      teamIDRules: [
        (v) => !!v || "teamID is none!",
        (v) =>
          (v.length >= 8 && v.length <= 12) ||
          "teamID must be 8-12 characters!",
        (v) =>
          /^((?!\\|\/|:|\*|\?|<|>|\||'|\.|%|#|&|\^).){1,20}$/.test(v) ||
          "Inlegal input",
      ],
      teamNameRules: [
        (v) => !!v || "teamname is none!",
        (v) =>
          (v.length >= 1 && v.length <= 30) ||
          "TeamName must be 1-30 characters!",
        (v) => /^[_a-zA-Z0-9]+$/.test(v) || "团队名必须由英文字母和下划线组成",
      ],
    },
  }),

  computed: {
    useringName() {
      return this.$store.state.user.username;
    },
  },

  methods: {
    //零，查询方法
    initialize() {
      // 获取全部文件,赋值
      console.log("initialize");
      this.axios
        .post(this.targetIP() + "/team.teamManage/teamInfo", {
          username: this.useringName,
        })
        .then((response) => {
          // console.log("initialize:" + response);
          // 设置用户列表
          if(response.data==false){
            this.teamList=this.defaultList;
          }
          else{
            const data = response.data;
            this.teamList = data;
          }
          
        })
        .catch((error) => {
          console.log(error);
        });
    },
    //一，创建团队
    createTeam() {
      //打开确认框
      this.state = "create";
      this.aDialog = true;
    },
    createTeamConfirm() {
      //创建用户调用
      this.axios
        .post(this.targetIP() + "/team.teamManage/createTeam", {
          team_name: this.editedItem.teamName,
          creator_name: this.useringName,
        })
        .then((response) => {
          console.log(response.data);
          if (response.data == false) {
            this.snackbarData = "TeamName Repeated or Inlegal!";
            this.snackbar = true;
          } else {
            this.snackbarData = "Congratulate,you team id is: " + response.data;
            this.snackbar = true;
            this.initialize();
          }
        })
        .catch((error) => {
          console.log(error);
        });
      this.closeDialog();
    },
    //二，解散团队()
    dismissTeam(team) {
      //打开确认框
      this.state = "dismiss";
      this.editedItem.teamName = team.teamName;
      this.editedItem.team_id = team.team_id;
      this.aDialog = true;
    },
    dismissTeamConfirm() {
      //解散团队调用
      if (this.editedItem.team_id == this.DismissConfirming) {
        this.axios
          .post(this.targetIP() + "/team.teamManage/disbandTeam", {
            team_name: this.editedItem.teamName,
            team_id: this.editedItem.team_id,
            username: this.useringName,
          })
          .then((response) => {
            console.log(response.data);
            if (response.data == true) {
              this.snackbarData = "You have dismiss this team!";
              this.snackbar = true;
              this.initialize();
            } else {
              console.log("joinMethod response.data:" + response.data);
              this.snackbarData = "Wrong dismiss!";
              this.snackbar = true;
            }
          })
          .catch((error) => {
            console.log(error);
          });
        this.closeDialog();
      } else {
        this.snackbarData = "You have enter wrong TeamID!";
        this.snackbar = true;
      }
    },
    //三，加入团队方法
    joinTeam() {
      //打开确认框
      this.state = "join";
      this.aDialog = true;
    },
    joinTeamConfirm() {
      //创建用户调用
      this.axios
        .post(this.targetIP() + "/team.teamManage/joinTeam", {
          team_id: this.editedItem.team_id,
          username: this.useringName,
        })
        .then((response) => {
          console.log(response.data);
          if (response.data == true) {
            this.snackbarData = "You have joint this team!";
            this.snackbar = true;
            this.initialize();
          } else {
            console.log("joinMethod response.data:" + response.data);
            this.snackbarData = "Wrong TeamID!";
            this.snackbar = true;
          }
        })
        .catch((error) => {
          console.log(error);
        });
      this.closeDialog();
    },

    //四，退出队伍方法
    leaveTeam(team) {
      //打开删除确认框
      console.log("lt");
      this.state = "leave";
      this.editedItem.team_id = team.team_id;
      this.editedItem.teamName = team.teamName;
      this.aDialog = true;
    },
    leaveTeamConfirm() {
      //在删除窗口确认时调用，即在确定窗口点击confirm
      this.axios
        .post(this.targetIP() + "/team.teamManage/exitTeam", {
          team_id: this.editedItem.team_id,
          username: this.useringName,
        })
        .then((response) => {
          console.log(response);
          this.initialize();
        })
        .catch((error) => {
          console.log(error);
        });

      this.closeDialog();
    },

    //
    //六，通用方法
    closeDialog() {
      //关闭确定窗，即在确定窗口点击canel
      this.aDialog = false;
      this.state = "";
      this.teamNumber = 0;

      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
      });
    },
    open(name, query) {
      // 需要先判断当前路由名称是否和目标路由一致，如果一致就不跳转
      if (this.$route.name !== name) {
        this.$router.push({
          name: name,
          query: query,
        });
      }
    },
    targetIP() {
      return this.$store.state.targetIP;
    },
  },

  created() {
    this.initialize();
  },
};
</script>